import * as jsonrpc from 'jsonrpc-lite';
import { indexer } from './indexer';
import userAgentParser from './user-agent';
import { CNL_API_VERSION, SDK_VERSION } from '../config';
import { delay } from './delay';
import ConnectLive, { SignStatus } from '../index';

type VersionParam = {
  version?: string;
};
type RequestParams = VersionParam & jsonrpc.RpcParams;

type JSONRpcRequestBody = {
  method: string;
  params: RequestParams;
};

type LogLevelNumber = {
  verbose: number;
  trace: number;
  debug: number;
  information: number;
  warning: number;
  error: number;
};

const LogLevel: LogLevelNumber = {
  verbose: 0,
	trace: 1,
  debug: 2,
	information: 3,
	warning: 4,
  error: 5
};

type LogLevelDesc = 'verbose' | 'trace' | 'debug' | 'information' | 'warning' | 'error';

export class LogSender {
  private ws: WebSocket | null = null;
  private keepAliveInterval = -1;
  private host: string = '';
  private token: string = '';
  private logLevelNumber: number = 3;
  private reconnectPeriod: number = -1;
  private reconnectTimeGap: number = 1000;
  collectionPeriod: number = 5;

  createWebSocket(host: string, token: string) {
    this.host = host;
    this.token = token;

    this.close();
    this.clearKeepAliveTimer();
    
    return new Promise((resolve)=>{
      this.ws = new WebSocket(host);
      this.ws.onopen = ()=>{
        const os = userAgentParser.getOS();
        const browser = userAgentParser.getBrowser();
        const device = userAgentParser.getDevice();

        const method = 'CnL.Registration';
        const params = {
          token: token,
          userInfo: {
            sdkVersion: SDK_VERSION,
            platform: 'web',
            platformVersion: `${browser.name}/${browser.version}`,
            osName: os.name,
            osVersion: os.version,
            deviceModel: device.model || '',
          }
        };
        this.send({ method, params });

        this.keepAliveInterval = window.setInterval(()=>{
          this.send({
            method: 'CnL.KeepAlive',
            params: {}
          });
        }, 50000);

        resolve(true);
      };
      this.ws.onerror = ()=>{
        if(ConnectLive.signStatus === SignStatus.SIGN_IN) {
          this.reconnect();
        }
      };
      this.ws.onclose = ()=>{
        if(ConnectLive.signStatus === SignStatus.SIGN_IN) {
          this.reconnect();
        }
      };
      this.ws.onmessage = (evt) => {
        const data = JSON.parse(evt.data);
        if(data.method === 'CnL.Push') {
          this.setLevel(data.params.config.log_level);
        }

        if(data.result) {
          if(data.result.config) {
            if(data.result.config.cnl) {
              this.reconnectPeriod = data.result.config.cnl.reconnect_period;
            }
            if(data.result.config.quality_metrics) {
              this.collectionPeriod = data.result.config.quality_metrics.collection_period;
            }
          }
        }
      }
    });
  }

  async reconnect() {
    this.close();
    
    if(this.reconnectPeriod > -1) {
      await delay(this.reconnectPeriod)
    }
    
    window.setTimeout(()=>{
      this.createWebSocket(this.host, this.token);
    }, this.reconnectTimeGap);
    if(this.reconnectTimeGap >= 16000) {
      this.reconnectTimeGap = 16000;
    } else {
      this.reconnectTimeGap = this.reconnectTimeGap * 2;
    }
  }

  async send<R = jsonrpc.Defined, E = jsonrpc.JsonRpcError>(body: JSONRpcRequestBody, isLog: boolean = false) {
    if(!this.ws) {
      return;
    }

    const method = body.method;
    const params: any = body.params;

    params.version = CNL_API_VERSION;

    if(method === 'CnL.Log') {
      const level: LogLevelDesc = params.logs[0].level;
      if(LogLevel[level] < this.logLevelNumber) {
        return;
      }
    }

    const id = indexer.get().toString();
    let stringified = '';
    if(!isLog) {
      stringified = JSON.stringify(jsonrpc.request(id, method, params));
    } else {
      stringified = JSON.stringify(jsonrpc.notification(method, params));  
    }

    if(this.ws.readyState !== this.ws.CONNECTING) {
      if(this.ws.readyState !== this.ws.CLOSED && this.ws.readyState !== this.ws.CLOSING) {
        this.ws.send(stringified);
      }
    } else {
      const timer = window.setInterval(()=>{
        if(this.ws?.readyState === this.ws?.OPEN) {
          window.clearInterval(timer);

          this.ws?.send(stringified);
        }
      }, 10);
    }
  }

  clearKeepAliveTimer() {
    window.clearInterval(this.keepAliveInterval);
    this.keepAliveInterval = -1;
  }

  close() {
    this.ws?.close();
    this.ws = null;
  }

  destroy() {
    this.close();
    this.clearKeepAliveTimer();

    this.logLevelNumber = 3;
    this.reconnectPeriod = -1;
    this.reconnectTimeGap = 1000;
  }

  setLevel (logLevel: LogLevelDesc): void {
		this.logLevelNumber = LogLevel[logLevel];
	}
}

export type LogParams = {
  sdkVersion: string;
  logs: Array<{
    date: string;
    level: string;
    message: string;
  }>;
};

export type LocalAudioQualityMetricReport = {
  type: 'audio';
  streamId: number;
  bytesSent: number;
  packetsSent: number;
  srcAudioLevel: number;
};

export type LocalVideoQualityMetricReport = {
  type: 'video';
  streamId: number;
  profile: 'l' | 'm' | 'h';
  bytesSent: number;
  framesEncoded: number;
  framesSent: number;
  pliCount: number;
  srcFrames: number;
};

export type RemoteAudioQualityMetricReport = {
  type: 'audio';
  receiverId: number;
  streamId: number;
  bytesReceived: number;
  totalSamplesReceived: number;
  audioLevel: number;
  packetsLost: number;
};

export type RemoteVideoQualityMetricReport = {
  type: 'video';
  receiverId: number;
  streamId: number;
  bytesReceived: number;
  framesReceived: number;
  framesDecoded: number;
  packetsLost: number;
  pliCount: number;
};

export type QualityMetricReport = {
  direction: 'up' | 'down';
  index: number;
  metrics: Array<LocalAudioQualityMetricReport | LocalVideoQualityMetricReport | RemoteAudioQualityMetricReport | RemoteVideoQualityMetricReport>
}

export type QualityMetricReportParams = {
  serviceId: string;
  roomId: string;
  participantId: string;
  sessions: QualityMetricReport[];
};