import { AuthApiClient, AuthConfig, AuthResponse } from './modules/room/auth-api-client';
import { SDK_VERSION } from './config';
import { logger, Logger, loggerSender, Loggable } from './utils/logger';
import { Room, RoomConfig } from './modules/room/room';
import { LocalAudio } from './modules/local/local-audio';
import { LocalVideo } from './modules/local/local-video';
import { LocalScreen } from './modules/local/local-screen';
import { LocalMedia } from './modules/local/local-media';
import EventEmitter from 'eventemitter3';
import userAgentParser from './utils/user-agent';
import { ClientError } from './utils/error-exception';
import { setStorage, getStorage } from './utils/storage';

export const enum SignStatus {
  SIGN_IN = 'signin',
  SIGN_OUT = 'signout',
};

class ConnectLive {
  private static emitter = new EventEmitter();
  private static authResponse: AuthResponse | null;
  private static authApiClient: AuthApiClient = new AuthApiClient();
  private static authRefreshTimer: number = -1;
  private static authConfig: AuthConfig;
  public static signStatus: SignStatus = SignStatus.SIGN_OUT;

  public static version: string = SDK_VERSION;
  public static logger: Logger = logger;

  constructor() {
    new ClientError(1901, 'ConnectLiveë¡ ë¶í± ì§ì  ê°ì²´ë¥¼ ì ì¸í  ì ììµëë¤.');
  }

  public static on<T = any>(eventName: string, callback: (eventData: T) => void) {
    this.emitter.on(eventName, callback);
  }

  private static _startAuthTimer() {
    if (this.authResponse) {
      const timeout = Math.floor(this.authResponse.ttl * 0.9 * 1000);
      let restTtl = timeout;
      const fn = async () => {
        if (this.authResponse) {
          try {
            const res = await this.authApiClient.refreshToken(this.authConfig.serviceId, this.authResponse.token);
            this.authResponse.token = res.token;
            this.authResponse.ttl = res.ttl;

            this._startAuthTimer();
          } catch (err) {
            logger.error('token refresh error');
            const timeout = 600000;

            restTtl = restTtl - timeout;
            if (restTtl < 0) {
              this.emitter.emit('tokenRefreshError', err);
            } else {
              this.emitter.emit('tokenRefreshWarning', err);
              window.setTimeout(async () => {
                fn();
              }, timeout);
            }
          }
        }
      };

      this.authRefreshTimer = window.setTimeout(async () => {
        fn();
      }, timeout);
    }
  }

  static async signIn(authConfig: AuthConfig) {
    if (this.authRefreshTimer > -1) {
      this.signOut();
    }
    const fingerprint = getStorage('connectlive-fingerprint') || '';
    this.authConfig = authConfig;

    this.authResponse = await this.authApiClient.authorize(authConfig, fingerprint);
    setStorage('connectlive-fingerprint', this.authResponse.fingerprint);

    this._startAuthTimer();

    await loggerSender.createWebSocket(this.authResponse.cnl, this.authResponse.token);

    logger.info('[method][Function.signIn]', authConfig);

    const device = userAgentParser.getDevice();
    const engine = userAgentParser.getEngine();
    const os = userAgentParser.getOS();
    const browser = userAgentParser.getBrowser();
    const cpu = userAgentParser.getCPU();

    logger.debug(
      'DEVICE TYPE =', device.type + ',',
      'DEVICE VENDOR =', device.vendor + ',',
      'DEVICE MODEL =', device.model + ',',
      'ENGINE NAME =', engine.name + ',',
      'ENGINE VERSION =', engine.version + ',',
      'OS NAME =', os.name + ',',
      'OS VERSION =', os.version + ',',
      'BROWSER NAME =', browser.name + ',',
      'BROWSER VERSION =', browser.version + ',',
      'CPU =', cpu.architecture + ',',
      'SDK VERSION =', SDK_VERSION
    );

    this.signStatus = SignStatus.SIGN_IN;
  }

  @Loggable('info')
  static signOut() {
    window.clearTimeout(this.authRefreshTimer);
    this.authRefreshTimer = -1;
    this.authResponse = null;

    this.signStatus = SignStatus.SIGN_OUT;

    loggerSender.destroy();
  }

  @Loggable('info')
  static async createLocalMedia(constraints: MediaStreamConstraints | undefined) {
    const localMedia = new LocalMedia(constraints || {});
    if (constraints) {
      await localMedia._getUserMedia();
    }
    return localMedia;
  }

  @Loggable('info')
  static async createLocalScreen(constraints: MediaStreamConstraints) {
    const localScreen = new LocalScreen(constraints);
    await localScreen._getDisplayMedia();
    return localScreen;
  }

  @Loggable('info')
  static createRoom(config?: Partial<RoomConfig>): Room {
    if (this.authResponse) {
      return new Room(this.authConfig, this.authResponse, config);
    }
    throw new ClientError(1106, 'ë£¸ ìì± ì  ì¸ì¦ì´ íìí©ëë¤.');
  }
}

