// Room.OnMessage
type OnMessageParams = {
  type: string;
  data?: object;
};

export type OnUserMessageParams = {
  sender: string;
  message: string;
  type: string;
}[];

// etc.
export type OnUpdatedReceivers = {
  name: 'init' | 'add' | 'remove';
  receivers: Receiver[];
};

type ReceiverTrackMap = {
  [receiverId: string]: MediaStreamTrack;
};

type ConfigureReceiverParams = {
  receivers: Array<{
    receiverId: string;
    pause?: boolean;
    profile?: 'l' | 'm' | 'h';
  }>;
};
type ConfigureReceiverResponse = {};

type DownStat = {
  mid: string;
  inboundRtp: any;
  track: any;
}

export type DownStats = {
  candidatePair: CustomRTCIceCandidatePairStats;
  audioStats: DownStat[];
  videoStats: DownStat[];
};