import { Loggable } from '../../utils/logger';
import { AudioTrackType } from '../session/up-session';
import { Local } from './local';

export class LocalAudio extends Local {
  type: AudioTrackType;
  isAlwaysOn: boolean = false;

  constructor(mediaStreamTrack: MediaStreamTrack, type: AudioTrackType, extraValue?: string) {
    super(mediaStreamTrack, extraValue);
    this.type = type;
  }

  @Loggable('info')
  stop() {
    this.mediaStreamTrack.stop();
  }

  @Loggable('info')
  async setAlwaysOn(enabled: boolean) {
    this.isAlwaysOn = typeof enabled === 'boolean' ? enabled : true;
    await this.statusUpdateFn({
      streamId: this.streamId,
      alwaysOn: this.isAlwaysOn,
    });
  }
}
