import { PROVISIONING_API_VERSION, PROVISIONING_SERVER_URL } from '../../config';
import { hash } from '../../utils/hash';
import { HttpWithJSONRpc } from '../../utils/http-with-json-rpc';
import { Loggable, logger } from '../../utils/logger';
import { ClientError, ServerError } from '../../utils/error-exception';

export class AuthApiClient extends HttpWithJSONRpc {
  constructor() {
    super();

    this.setHeaders({ method: 'post', headers: { 'Content-Type': 'application/json' } });
  }

  @Loggable('info')
  authorize(authConfig: AuthConfig, fingerprint: string = ''): Promise<AuthResponse> {
    const host = authConfig.endpoint || PROVISIONING_SERVER_URL;
    this.setHost(host);

    if (!('token' in authConfig)) return this.internally(authConfig as InternalAuthConfig, fingerprint);
    else return this.externally(authConfig, fingerprint);    
  };

  @Loggable('info')
  async refreshToken(serviceId: string, oldToken: string): Promise<RefreshTokenResponse> {
    const method = 'ExtendTTL';
    const params: RefreshTokenParams = { serviceId: serviceId, token: oldToken };
    const response = await this.request<RefreshTokenResponse>({ method, params });

    if ('error' in response) {
      logger.error('refreshToken', response);
      const error: Error = new ServerError(response.error.code, response.error.message);
      throw error;
    }

    const { result } = response;
    return result;
  };

  @Loggable('info')
  private async internally (config: InternalAuthConfig, fingerprint: string): Promise<AuthResponse> {
    const { serviceId, serviceSecret } = config;
    const texts = serviceSecret.split(':');
    const serviceKey = texts[0];
    const secret = texts[1];

    if(!serviceId || !serviceKey || !secret) {
      throw new ClientError(1103, 'ì¸ì¦ì ìí ìë¹ì¤ ì ë³´ê° ìë ¥ëì§ ìììµëë¤.');
    }

    const scheme = 'internal';
    const method = 'Provision';
    const firstParams: InternalFirstTransactionParams = {
      scheme,
      serviceId,
      version: PROVISIONING_API_VERSION
    };
    const firstResponse = await this.request<void, InternalFirstTransactionError>({
      method,
      params: firstParams,
    });

    if ('result' in firstResponse) {
      logger.error('internally', firstResponse);
      throw new Error('Provisioning Failed');
    }

    if (!('data' in firstResponse.error)) {
      logger.error('internally', firstResponse);
      const error: Error = new ServerError(firstResponse.error.code, firstResponse.error.message);
      throw error;
    }

    const { nonce } = firstResponse.error.data;
    const ha = await hash([serviceId, serviceKey, secret].join(':'));
    const value = await hash([ha, nonce].join(':'));
    const secondParams: InternalSecondTransactionParams = {
      version: PROVISIONING_API_VERSION,
      serviceId,
      scheme,
      fingerprint,
      auth: {
        nonce,
        key: serviceKey,
        value,
      },
    };
    const secondResponse = await this.request<AuthResponse>({ method, params: secondParams });

    if ('error' in secondResponse) {
      logger.error('internally', secondResponse);
      const error: Error = new ServerError(secondResponse.error.code, secondResponse.error.message);
      throw error;
    }

    const { result } = secondResponse;
    return result;
  };

  @Loggable('info')
  private async externally(config: ExternalAuthConfig, fingerprint: string): Promise<AuthResponse> {
    const { token, serviceId } = config;
    if(!serviceId || !token) {
      throw new ClientError(1103, 'ì¸ì¦ì ìí ìë¹ì¤ ì ë³´ê° ìë ¥ëì§ ìììµëë¤.');
    }

    const scheme = 'external';
    const method = 'Provision';
    const params: ExternalTransactionParams = {
      serviceId,
      scheme,
      fingerprint,
      auth: { value: token }
    };
    const response = await this.request<AuthResponse>({ method, params });

    if ('error' in response) {
      logger.error('externally', response);
      const error: Error = new ServerError(response.error.code, response.error.message);
      throw error;
    }

    const { result } = response;
    return result;
  };
}

export type InternalAuthConfig = {
  endpoint?: string;
  serviceId: string;
  serviceSecret: string;
};
export type ExternalAuthConfig = {
  endpoint?: string;
  serviceId: string;
  token: string;
};
export type AuthConfig = InternalAuthConfig | ExternalAuthConfig;
export type AuthResponse = {
  uuid: string;
  token: string;
  ttl: number; // token ttl in second. ex) 3600 = 1 hour
  api: string; // api server urls
  cnl: string; // configuration and logging server url
  fingerprint: string;
  iceServers: Array<{
    urls: string[];
    username?: string;
    credential?: string;
  }>;
};
type InternalFirstTransactionParams = {
  version?: string;
  serviceId: string;
  scheme: 'internal';
};
type InternalFirstTransactionError = {
  code: number;
  message: string;
  data: { nonce: string };
};
type InternalSecondTransactionParams = {
  version?: string;
  serviceId: string;
  scheme: 'internal';
  fingerprint?: string;
  auth: {
    nonce: string;
    key: string;
    value: string;
  };
};
type ExternalTransactionParams = {
  version?: string;
  serviceId: string;
  scheme: 'external';
  fingerprint?: string;
  auth: {
    value: string;
  };
};
type RefreshTokenParams = {
  serviceId: string;
  token: string;
};
type RefreshTokenResponse = {
  token: string;
  ttl: number;
};
