import { Loggable } from "../../utils/logger";

export abstract class Local {
  extraValue?: string;
  protected mediaStreamTrack: MediaStreamTrack;
  streamId: number = -1;
  protected statusUpdateFn: StatusUpdateFn = () => Promise.resolve();

  constructor(mediaStreamTrack: MediaStreamTrack, extraValue?: string) {
    this.mediaStreamTrack = mediaStreamTrack;

    this.extraValue = extraValue;
  }

  get isEnabled() {
    return this.mediaStreamTrack.enabled;
  }

  get kind() {
    return this.mediaStreamTrack.kind;
  }

  @Loggable('debug')
  getMediaStreamTrack() {
    return this.mediaStreamTrack;
  }

  @Loggable('debug')
  getMediaStream() {
    return new MediaStream([this.mediaStreamTrack]);
  }

  @Loggable('info')
  getStreamId() {
    return this.streamId;
  }

  @Loggable('info')
  getExtraValue() {
    return this.extraValue;
  }

  @Loggable('info')
  setExtraValue (value: string) {
    this.extraValue = value;
  }

  @Loggable('info')
  async setEnabled(enabled: boolean) {
    enabled = typeof enabled === 'boolean' ? enabled : true;
    if (enabled !== this.mediaStreamTrack.enabled) {
      this.mediaStreamTrack.enabled = enabled;
      await this.statusUpdateFn({
        streamId: this.streamId,
        active: enabled,
      });
    }
  }

  abstract stop(): void;

  /**
   * @internal mute on/off ì ë³´ë¥¼ ë¯¸ëì´ ìë²ì ë°ìíê¸° ìí setup
   */
  _setStreamId(streamId: number, statusUpdateFn: StatusUpdateFn) {
    this.streamId = streamId;
    this.statusUpdateFn = statusUpdateFn;
  }
}

export type StatusUpdateFn = (config: { streamId: number; active?: boolean; alwaysOn?: boolean }) => Promise<void>;
