import { HttpWithJSONRpc } from '../../utils/http-with-json-rpc';
import userAgentParser from '../../utils/user-agent';
import { SDK_VERSION } from '../../config';
import { Loggable } from '../../utils/logger';
import EventEmitter from 'eventemitter3';
import { ServerError } from '../../utils/error-exception';

export class RoomApiClient extends HttpWithJSONRpc {
  private roomId!: string;
  private participantId!: string;
  private sign!: string;
  protected emitter: EventEmitter;

  constructor() {
    super();

    this.emitter = new EventEmitter();
  }

  @Loggable('info')
  async setRoomId (roomId: string) {
    this.roomId = roomId;
  };

  @Loggable('info')
  async setParticipantId (participantId: string) {
    this.participantId = participantId;
  };

  @Loggable('info')
  async sendEnterRoom () {
    const os = userAgentParser.getOS();
    const browser = userAgentParser.getBrowser();

    const method = 'Room.EnterRoom';
    const params: EnterRoomParams = {
      roomId: this.roomId,
      userInfo: {
        sdkVersion: SDK_VERSION,
        platform: 'web',
        platformVersion: `${browser.name}/${browser.version}`,
        osName: os.name,
        osVersion: os.version,
        deviceModel: '',
      }
    };
    const response = await this.request<EnterRoomResponse>({ method, params });

    if ('error' in response) {
      throw new Error(JSON.stringify(response.error));
    }

    this.sign = response.result.sign;
    return response;
  };

  @Loggable('debug')
  async sendCreateSession (direction: string, index: number, sdp: CreateSessionParams['desc']) {
    const method = 'Room.CreateSession';
    const params: CreateSessionParams = {
      roomId: this.roomId,
      participantId: this.participantId,
      sign: this.sign,
      direction,
      index,
      desc: sdp,
    };
    const response = await this.request<CreateSessionResponse>({ method, params });
    if ('error' in response) {
      //ìë¬ì ë³µêµ¬ ë¶ê°ë¥ ìë¬ë¡ ìë¬ ì´ë²¤í¸ í¸ì¶
      const error = new ServerError(response.error.code, response.error.message);
      this.emitter.emit('error', error);
      throw error;
    }
    return response;
  };

  @Loggable('debug')
  async sendAddCandidate (direction: string, index: number, candidate: AddCandidateParams['candidate']) {
    const method = 'Room.AddCandidate';
    const params: AddCandidateParams = {
      roomId: this.roomId,
      participantId: this.participantId,
      sign: this.sign,
      direction,
      index,
      candidate,
    };
    const response = await this.request<AddCandidateResponse>({ method, params });
    if ('error' in response) {
      const error = new ServerError(response.error.code, response.error.message);
      if(response.error.code === -23005) {
        //ìë¬ì ë³µêµ¬ ë¶ê°ë¥ ìë¬ë¡ ìë¬ ì´ë²¤í¸ í¸ì¶
        this.emitter.emit('error', error);
      }
      throw error;
    }
    return response;
  };

  @Loggable('debug')
  async sendRestartIce (direction: string, index: number, sdp: RestartIceParams['desc']) {
    const method = 'Room.RestartIce';
    const params: RestartIceParams = {
      roomId: this.roomId,
      participantId: this.participantId,
      sign: this.sign,
      direction,
      index,
      desc: sdp
    };
    const response = await this.request<RestartIceResponse>({ method, params });
    if ('error' in response) {
      const error = new ServerError(response.error.code, response.error.message);
      if(response.error.code === -23005) {
        //ìë¬ì ë³µêµ¬ ë¶ê°ë¥ ìë¬ë¡ ìë¬ ì´ë²¤í¸ í¸ì¶
        this.emitter.emit('error', error);
      }
      throw error;
    }
    return response;
  };

  @Loggable('info')
  async sendDestroySession (direction: string, index: number) {
    const method = 'Room.DestroySession';
    const params: DestroySessionParams = {
      roomId: this.roomId,
      participantId: this.participantId,
      sign: this.sign,
      direction,
      index,
    };
    const response = await this.request<DestroySessionResponse>({ method, params });
    if ('error' in response) {
      const error = new ServerError(response.error.code, response.error.message);
      if(response.error.code === -23005) {
        //ìë¬ì ë³µêµ¬ ë¶ê°ë¥ ìë¬ë¡ ìë¬ ì´ë²¤í¸ í¸ì¶
        this.emitter.emit('error', error);
      }
      throw error;
    }
    return response;
  };

  on<T = any>(name: string, callback: (eventData: T) => void) {
    this.emitter.on(name, callback);
  }
}

type EnterRoomParams = {
  roomId: string;
  userInfo?: {
    sdkVersion?: string;
    platform?: string;
    platformVersion?: string;
    osName?: string;
    osVersion?: string;
    deviceModel?: string;
  }
};
type EnterRoomResponse = {
  roomId: string;
  participantId: string;
  sign: string;
};

type CreateSessionParams = {
  roomId: string;
  participantId: string;
  sign: string;
  direction: string;
  index: number;
  desc: RTCSessionDescriptionInit;
};
type CreateSessionResponse = {
  desc: RTCSessionDescriptionInit;
};

type AddCandidateParams = {
  roomId: string;
  participantId: string;
  sign: string;
  direction: string;
  index: number;
  candidate: RTCIceCandidate;
};
type AddCandidateResponse = {};

type RestartIceParams = {
  roomId: string;
  participantId: string;
  sign: string;
  direction: string;
  index: number;
  desc: RTCSessionDescriptionInit;
};
type RestartIceResponse = CreateSessionResponse;

type DestroySessionParams = {
  roomId: string;
  participantId: string;
  sign: string;
  direction: string;
  index: number;
};
type DestroySessionResponse = {};
