import * as jsonrpc from 'jsonrpc-lite';
import { indexer } from './indexer';
import { logger } from './logger';
import { ClientError, ServerError } from './error-exception';
import { ROOM_API_VERSION } from '../config';

type HttpHeaders = RequestInit;
type HttpMethod = RequestInit['method'];
type RequestOptions = {
  httpMethod?: RequestInit['method'];
  path?: string;
};

type VersionParam = {
  version?: string;
};
type RequestParams = VersionParam & jsonrpc.RpcParams;
type JSONRpcRequestBody = {
  method: string;
  params: RequestParams;
};
type SuccessObject<T> = Omit<jsonrpc.SuccessObject, 'result'> & { result: T };
type ErrorObject<T> = Omit<jsonrpc.ErrorObject, 'error'> & { error: T };

type ServerErrorObject = {
  code: number;
  data: any;
  message: string;
};

export class HttpWithJSONRpc {
  private headers: HttpHeaders = {};
  private host: string = '';

  setHost(host: string) {
    this.host = host;
  }

  setHeaders(headers: HttpHeaders) {
    this.headers = headers;
  }

  protected async request<R = jsonrpc.Defined, E = jsonrpc.JsonRpcError>(
    body: JSONRpcRequestBody,
    options: RequestOptions = {},
  )
   {
    try{
      const { method, params } = body;
      if(method !== 'Provision' && method !== 'ExtendTTL') {
        params.version = ROOM_API_VERSION;
      }
      const { httpMethod = 'post', path = '' } = options;
      const id = indexer.get().toString();
      const stringified = JSON.stringify(jsonrpc.request(id, method, params));
      const url = this.host + path;
      logger.trace(`[${method}] ${stringified}`);
      const res = await fetch(url, { ...this.headers, method: httpMethod, body: stringified });
      const data = (await res.json()) as SuccessObject<R> | ErrorObject<ServerErrorObject>;

      if(method !== 'Provision' && method !== 'ExtendTTL') {
        if ('error' in data) {
          logger.error(data);
          const error: Error = new ServerError(data.error.code, data.error.message);
          throw error;
        }
      }

      logger.trace(`[RESULT] ${JSON.stringify(data)}`);
      return data;
    } catch(err){
      const e = err as Error;
      const message = e.message;
      logger.error(message);
      
      const error: Error = new ClientError(1904, 'HTTP ì¸ìì ì¤ë¥ê° ë°ìíìµëë¤.', e);
      throw error;
    }
  }
}
