import EventEmitter from 'eventemitter3';
import * as jsonrpc from 'jsonrpc-lite';
import { ROOM_API_VERSION } from '../config';
import { indexer } from './indexer';

export type RequestObject = jsonrpc.RequestObject | jsonrpc.NotificationObject;
export type SuccessObject<T> = Omit<jsonrpc.SuccessObject, 'result'> & { result: T };
export type ErrorObject<T> = Omit<jsonrpc.ErrorObject, 'error'> & { error: T };
export type NotificationObject<T> = Omit<jsonrpc.NotificationObject, 'params'> & { params: T };
export type NotificationCallback<T> = (notification: NotificationObject<T>) => void;

type VersionParam = {
  version?: string;
};
type RequestParams = VersionParam & jsonrpc.RpcParams;

export class JSONRpc {
  private emitter: EventEmitter;
  private methodMap: Map<string, string>;
  toStream: (payload: RequestObject) => void;

  constructor() {
    this.emitter = new EventEmitter();
    this.methodMap = new Map();
    this.toStream = () => console.warn('Please set toStream function!');
  }

  /**
   * handle stringified message.
   * parse -> emit
   */
  handleMessage(stringified: string) {
    const parsedObjects = jsonrpc.parse(stringified);
    const isArray = Array.isArray(parsedObjects);

    const handleParsedObject = (parsedObject: jsonrpc.IParsedObject) => {
      const { payload } = parsedObject;
      const hasId = 'id' in payload;
      const hasMethod = 'method' in payload;

      if (hasId && !hasMethod) {
        // = success or error
        const id = String(payload.id);
        const method = this.methodMap.get(id);

        this.emitter.emit(id, payload);
        // FIXME: ìë ê¼­ íìíê±´ì§ íì¸í  íì ìì
        if (method) this.emitter.emit(method, payload);
        return;
      }

      if (hasMethod && !hasId) {
        // = notification
        const method = payload.method;
        return this.emitter.emit(method, payload);
      }

      console.error('invalid message', parsedObject);
    };

    if (!isArray) {
      handleParsedObject(parsedObjects);
    } else {
      parsedObjects.forEach(handleParsedObject);
    }
  }

  /**
   * Send json rpc message by using `toStream` function,
   * and receive result
   */
  call<R = jsonrpc.Defined, E = jsonrpc.JsonRpcError>(method: string, params: RequestParams) {
    params.version = ROOM_API_VERSION;
    return new Promise<SuccessObject<R> | ErrorObject<E>>(resolve => {
      const id = indexer.get().toString();
      this.methodMap.set(id, method);
      this.emitter.once(id, resolve);

      const requestObject = jsonrpc.request(id, method, params);
      this.toStream(requestObject);
    });
  }

  on<T = jsonrpc.RpcParams>(method: string, callback: NotificationCallback<T>) {
    this.emitter.on(method, callback);
  }

  off<T = jsonrpc.RpcParams>(method: string, callback: NotificationCallback<T>) {
    this.emitter.off(method, callback);
  }
}
