import { ClientError } from '../../utils/error-exception';
import { Loggable } from '../../utils/logger';
import { RemoteParticipant } from '../participant/remote-participant';
import { ParticipantStream } from '../session/down-session';
import { VideoTrackType } from '../session/up-session';
import { Remote, StatusUpdateFn } from './remote';

export class RemoteVideo extends Remote{
  type: VideoTrackType = 'none';
  extraValue: string = '';
  videoElements: HTMLVideoElement[];
  mediaStreamTrack?: MediaStreamTrack;
  videoId: number = -1;
  receiverId: string = '';
  paused: boolean = false; //ë´ê° ìì íì§ ìì
  active: boolean = true; //í´ë¹ ì°¸ì¬ìê° ìì ì ë¹ëì¤ì activeë¥¼ ë³ê²½í¨
  profile: 'l' | 'm' | 'h' = 'l';
  statusUpdateFn: StatusUpdateFn = () => Promise.resolve();

  constructor(stream: ParticipantStream, participant: RemoteParticipant) {
    super(participant.id);

    this.videoId = stream.streamId;
    this.videoElements = [];

    this.type = stream.type;
    this.extraValue = stream.extraValue || '';
  }

  get isEnabled() {
    return this.mediaStreamTrack?.enabled;
  }

  @Loggable('info')
  _setType(type: VideoTrackType) {
    this.type = type;
  }

  @Loggable('info')
  _setRemoteTrack(mediaStreamTrack: MediaStreamTrack) {
    this.mediaStreamTrack = mediaStreamTrack;
  }

  @Loggable('info')
  attach(element: HTMLVideoElement | undefined = undefined) {
    if(!this.mediaStreamTrack) {
      return;
    }
    let videoElement;
    if(element) {
      videoElement = element;
    } else {
      videoElement = document.createElement('video');
    }

    const mediaStream = this.getMediaStream();
    videoElement.srcObject = mediaStream ? mediaStream : null;
    videoElement.autoplay = true;
    videoElement.playsInline = true;
    this.videoElements.push(videoElement);
    return videoElement;
  }

  @Loggable('info')
  detach() {
    this.videoElements.forEach(element => element.remove());
    this.videoElements = [];
  }

  @Loggable('info')
  getMediaStreamTrack() {
    return this.mediaStreamTrack;
  }

  @Loggable('info')
  getMediaStream() {
    if(this.mediaStreamTrack) {
      return new MediaStream([this.mediaStreamTrack]);
    }
  }

  @Loggable('info')
  getVideoId() {
    return this.videoId;
  }

  @Loggable('info')
  _setVideoId(streamId: number) {
    this.videoId = streamId;
  }

  @Loggable('debug')
  _setReceiverId(receiverId: string, statusUpdateFn: StatusUpdateFn) {
    this.receiverId = receiverId;
    this.statusUpdateFn = statusUpdateFn;
  }

  @Loggable('info')
  _setActive(enabled: boolean) {
    this.active = enabled;
  }

  @Loggable('info')
  async setPaused(enabled: boolean) {
    if(this.mediaStreamTrack) {
      enabled = typeof enabled === 'boolean' ? enabled : true;

      this.paused = enabled;
      if (enabled !== this.mediaStreamTrack.enabled) {
        this.mediaStreamTrack.enabled = enabled;
        await this.statusUpdateFn({
          receiverId: this.receiverId,
          pause: enabled,
        });
      }
    }
  }

  @Loggable('info')
  async setQuality(quality: 'l' | 'm' | 'h') {
    if(quality !== 'l' && quality !== 'm' && quality !== 'h') {
      throw new ClientError(1207, "ë¹ëì¤ íì§ ë³ê²½ ì¸ìë 'l', 'm', ''h ì¤ íëì¬ì¼ í©ëë¤.");
    }
    if (quality) {      
      this.profile = quality;
      await this.statusUpdateFn({
        receiverId: this.receiverId,
        profile: quality,
      });
    }
  }
}