import { LocalAudio } from '../local/local-audio';
import { LocalVideo } from '../local/local-video';
import { ParticipantStream } from '../session/down-session';
import { objectToArray } from '../../utils/object-util';
import { Loggable } from '../../utils/logger';

export class LocalParticipant {
  id: string = '';

  audioMap: LocalAudioMap = {};
  videoMap: LocalVideoMap = {};

  get hasAudio() {
    return objectToArray(this.audioMap).length > 0;
  }

  get hasVideo() {
    return objectToArray(this.videoMap).length > 0;
  }

  @Loggable('info')
  _setLocalAudio(audio: LocalAudio) {
    this.audioMap[audio.streamId] = audio;
  }

  @Loggable('info')
  _setLocalVideo(video: LocalVideo) {
    this.videoMap[video.streamId] = video;
  }

  @Loggable('info')
  getAudio(streamId: number): LocalAudio | null {
    return this.audioMap[streamId] || null;
  }

  @Loggable('info')
  getVideo(streamId: number): LocalVideo | null {
    return this.videoMap[streamId] || null;
  }

  @Loggable('info')
  _setLocalParticipantId(participantId: string) {
    this.id = participantId;
  }

  @Loggable('info')
  stop() {
    objectToArray(this.audioMap).forEach(audio => audio.stop());
    objectToArray(this.videoMap).forEach(video => video.stop());
  }

  /**
   * @internal
   */
  @Loggable('debug')
   _removeStream(streamId: number) {
    delete this.audioMap[streamId];
    delete this.videoMap[streamId];
  }
}

type LocalAudioMap = {
  [streamId: ParticipantStream['streamId']]: LocalAudio;
};
type LocalVideoMap = {
  [streamId: ParticipantStream['streamId']]: LocalVideo;
};