abstract class ErrorException extends Error {
  code: number;

  constructor(code: number, message: string) {
    super(message);
    this.code = code;

    Object.setPrototypeOf(this, ErrorException.prototype);
  }

  getCode() {
    return this.code;
  }

  getMessage() {
    return this.message;
  }
}

export { ErrorException };

class ServerError extends ErrorException {
  constructor(code: number, message: string) {
    super(code, message);
    this.name = 'ServerError';
  }
}

export { ServerError };

class ClientError extends ErrorException {
  origin?: Error;

  constructor(code: number, message: string, origin?: Error) {
    super(code, message);
    this.name = 'ClientError';
    this.origin = origin;
  }
}

export { ClientError };
