export const some = <K extends string | number, V>(object: Record<K, V>, fn: (value: V) => boolean): boolean => {
  for (const key in object) {
    if (fn(object[key])) return true;
  }
  return false;
};

export const filterToArray = <K extends string | number, V>(object: Record<K, V>, fn: (value: V) => boolean): V[] => {
  const filtered: V[] = [];
  for (const key in object) {
    if (fn(object[key])) filtered.push(object[key]);
  }
  return filtered;
};

export const filter = <K extends string | number, V>(object: Record<K, V>, fn: (value: V) => boolean): Record<K, V> => {
  const filtered = {} as Record<K, V>;
  for (const key in object) {
    if (fn(object[key])) filtered[key] = object[key];
  }
  return filtered;
};

export const size = <K extends string | number, V>(object: Record<K, V>): number => {
  return Object.keys(object).length;
};

export const objectToArray = (object: any): any[] => {
  return Object.values(object);
}