import { ClientError } from '../../utils/error-exception';
import { Loggable, logger } from '../../utils/logger';
import { Room } from '../room/room';
import { LocalAudio } from './local-audio';
import { LocalVideo } from './local-video';

class LocalScreen {
  room: Room | null = null;
  constraints: MediaStreamConstraints;
  audio: LocalAudio | undefined = undefined;
  video: LocalVideo | undefined = undefined;

  constructor(constraints: MediaStreamConstraints = {}) {
    if(constraints.video) {
      if(constraints.video === true) constraints.video = {};
    }

    if(constraints.audio) {
      if(constraints.audio === true) constraints.audio = {};
    }
    this.constraints = constraints;
  }

  @Loggable('debug')
  async switchDisplay() {
    if(this.constraints) {
      if(this.constraints.video) {

        if(this.room && this.video) {
          await this.room.unpublish([this]);
        }

        await this._getDisplayMedia();

        if(this.room && this.video)
          this.room.publish([this]);
      }
    }
  }

  @Loggable('info')
  async _getDisplayMedia () {
    this.stop();
    
    try {
      const mediaStream = await navigator.mediaDevices.getDisplayMedia(this.constraints);
    
      const [audioTrack] = mediaStream.getAudioTracks();
      const [videoTrack] = mediaStream.getVideoTracks();

      this.audio = audioTrack ? new LocalAudio(audioTrack, 'single') : undefined;
      this.video = videoTrack ? new LocalVideo(videoTrack, 'single') : undefined;

      logger.debug('got display stream', 'id=' + mediaStream.id, 'active=' + mediaStream.active);

      if(audioTrack) {
        logger.debug('got display audio track', 'id=' + audioTrack.id, 'label=' + audioTrack.label, 'enabled=' + audioTrack.enabled, 'kind=' + audioTrack.kind, 'muted=' + audioTrack.muted, 'readyState=' + audioTrack.readyState);

        if(audioTrack.readyState === 'ended') {
          throw new ClientError(1205, 'ì¤ëì¤ í¸ëì íëíì§ë§ ì¢ë£ ìíìëë¤.'); 
        }
      }

      if(videoTrack) {
        logger.debug('got display video track', 'id=' + videoTrack.id, 'label=' + videoTrack.label, 'enabled=' + videoTrack.enabled, 'kind=' + videoTrack.kind, 'muted=' + videoTrack.muted, 'readyState=' + videoTrack.readyState);

        if(videoTrack.readyState === 'ended') {
          throw new ClientError(1205, 'ë¹ëì¤ í¸ëì íëíì§ë§ ì¢ë£ ìíìëë¤.'); 
        }
      }

      this.video?.setHd(true);

      if(audioTrack) logger.debug('got display media track', 'id=' + audioTrack.id, 'label=' + audioTrack.label, 'enabled=' + audioTrack.enabled, 'kind=' + audioTrack.kind, 'muted=' + audioTrack.muted, 'readyState=' + audioTrack.readyState);
      if(videoTrack) logger.debug('got display media track', 'id=' + videoTrack.id, 'label=' + videoTrack.label, 'enabled=' + videoTrack.enabled, 'kind=' + videoTrack.kind, 'muted=' + videoTrack.muted, 'readyState=' + videoTrack.readyState);
    } catch(err) {
      const e = err as Error;
      const message = e.message;
      logger.error('Failed to getDisplayMedia. ', e.name, message);

      throw new ClientError(1204, `getDisplayMedia ì¤í¨(${message})`, e);
    }
  }

  @Loggable('info')
  stop() {
    this.audio?.stop();
    this.video?.stop();
  }

  _setRoom(room: Room) {
    this.room = room;
  }
}

export { LocalScreen };