import { Loggable } from "../../utils/logger";
import { RemoteParticipant } from "../participant/remote-participant";
import { ParticipantStream } from "../session/down-session";
import { AudioTrackType } from "../session/up-session";
import { Remote } from "./remote";

export class RemoteAudio extends Remote {
  type: AudioTrackType = 'none';
  extraValue: string = '';
  audioId: number = -1;
  receiverId: string = '';
  isAlwaysOn?: boolean;
  active: boolean = true; //í´ë¹ ì°¸ì¬ìê° ìì ì ì¤ëì¤ì activeë¥¼ ë³ê²½í¨

  constructor(stream: ParticipantStream, participant: RemoteParticipant) {
    super(participant.id);

    this.audioId = stream.streamId;
    this.type = stream.type as AudioTrackType;
    this.extraValue = stream.extraValue as string;
    this.isAlwaysOn = stream.property.alwaysOn;
    this.active = stream.property.active;
  }

  @Loggable('info')
  getAudioId() {
    return this.audioId;
  }

  @Loggable('debug')
  _setReceiverId(receiverId: string) {
    this.receiverId = receiverId;
  }

  @Loggable('info')
  _setAlwaysOn(alwaysOn?: boolean) {
    this.isAlwaysOn = alwaysOn;
  }

  @Loggable('info')
  _setActive(active: boolean) {
    this.active = active;
  }
}
