import { Loggable } from '../../utils/logger';
import { VideoTrackType } from '../session/up-session';
import { Local } from './local';

export class LocalVideo extends Local {
  videoElements: HTMLVideoElement[];
  type: VideoTrackType;
  hd: boolean = false;
  cloneMediaStreamTracks: MediaStreamTrack[];

  constructor(mediaStreamTrack: MediaStreamTrack, type: VideoTrackType, extraValue?: string) {
    super(mediaStreamTrack, extraValue);
    this.type = type;
    this.videoElements = [];
    this.cloneMediaStreamTracks = [];
  }

  @Loggable('debug')
  attach(element: HTMLVideoElement | undefined = undefined) {
    if(!this.mediaStreamTrack) {
      return;
    }
    let videoElement;
    if(element) {
      videoElement = element;
    } else {
      videoElement = document.createElement('video');
    }

    const mediaStream = this.getMediaStream();
    videoElement.srcObject = mediaStream ? mediaStream : null;
    videoElement.autoplay = true;
    videoElement.playsInline = true;
    this.videoElements.push(videoElement);
    return videoElement;
  }

  @Loggable('debug')
  detach() {
    this.videoElements.forEach(element => element.remove());
    this.videoElements = [];
  }

  @Loggable('info')
  stop() {
    this.mediaStreamTrack.stop();
    this.cloneMediaStreamTracks.forEach(track => track.stop());

    this.cloneMediaStreamTracks = [];
  }

  @Loggable('info')
  setHd(enabled: boolean) {
    this.hd = enabled;
  }

  @Loggable('debug')
  _getCloneTrack() {
    const cloneTrack = this.getMediaStreamTrack().clone();
    this.cloneMediaStreamTracks.push(cloneTrack);
    return cloneTrack;
  }
}
