import { Loggable } from "../../utils/logger";

export abstract class Remote {
  extraValue: string = '';
  participantId: string = '';

  constructor(participantId: string) {
    this.participantId = participantId;
  }

  @Loggable('info')
  getParticipantId() {
    return this.participantId;
  }

  @Loggable('info')
  getExtraValue() {
    return this.extraValue;
  }

  @Loggable('info')
  _setExtraValue(extraValue: string = '') {
    this.extraValue = extraValue;
  }

  @Loggable('debug')
  _setParticipantId(participantId: string) {
    this.participantId = participantId;
  }
}

export type StatusUpdateFn = (config: { receiverId: string; pause?: boolean; profile?: 'l' | 'm' | 'h' }) => Promise<void>;