import { ClientError } from '../../utils/error-exception';
import { logger, Loggable } from '../../utils/logger';
import { Room } from '../room/room';
import { LocalAudio } from './local-audio';
import { LocalVideo } from './local-video';

class LocalMedia {
  room: Room | null = null;
  constraints: MediaStreamConstraints;
  audio: LocalAudio | undefined = undefined;
  video: LocalVideo | undefined = undefined;

  constructor(constraints: MediaStreamConstraints = {}) {
    if(constraints.video) {
      if(constraints.video === true) constraints.video = {};
    }

    if(constraints.audio) {
      if(constraints.audio === true) constraints.audio = {};
    }
    this.constraints = constraints;
  }

  @Loggable('debug')
  async getDevices(): Promise<MediaDeviceInfo[]> {
    return await navigator.mediaDevices.enumerateDevices();
  }

  @Loggable('debug')
  async getMicDevices(): Promise<MediaDeviceInfo[]> {
    return (await this.getDevices()).filter(device => device.kind === 'audioinput' && device.deviceId);
  }

  @Loggable('debug')
  async getSpeakerDevices(): Promise<MediaDeviceInfo[]> {
    return (await this.getDevices()).filter(device => device.kind === 'audiooutput' && device.deviceId);
  }

  @Loggable('debug')
  async getCameraDevices(): Promise<MediaDeviceInfo[]> {
    return (await this.getDevices()).filter(device => device.kind === 'videoinput' && device.deviceId);
  }

  @Loggable('info')
  async switchCamera(deviceId: string) {
    if(this.constraints) {
      if(this.constraints.video) {
        this.constraints.video = Object.assign(this.constraints.video, { deviceId: { exact: deviceId } });

        if(this.room && this.video) {
          this.video.stop();
          await this.room.unpublish([this.video]);
        }

        let mediaStream: MediaStream;
        try {
          mediaStream = await navigator.mediaDevices.getUserMedia({
            video: this.constraints.video,
            audio: false
          });
        } catch(err) {
          const e = err as Error;
          const message = e.message;
          logger.error('Failed to switchCamera. ', e.name, message);
    
          throw new ClientError(1203, `getUserMedia ì¤í¨(${message})`, e);
        }

        const [videoTrack] = mediaStream.getVideoTracks();
        if(videoTrack) {
          if(videoTrack.readyState === 'ended') {
            throw new ClientError(1206, 'ë¹ëì¤ í¸ëì íëíì§ë§ ì¢ë£ ìíìëë¤.'); 
          }
          
          this.video = new LocalVideo(videoTrack, 'multiple');
          logger.debug('switch video track', 'id=' + videoTrack.id, 'label=' + videoTrack.label, 'enabled=' + videoTrack.enabled, 'kind=' + videoTrack.kind, 'muted=' + videoTrack.muted, 'readyState=' + videoTrack.readyState);

          if(this.room) await this.room.publish([this.video]);
        }
      }
    }
  }

  @Loggable('info')
  async switchMic(deviceId: string) {
    if(this.constraints) {
      if(this.constraints.audio) {
        this.constraints.audio = Object.assign(this.constraints.audio, { deviceId: { exact: deviceId } });

        if(this.room && this.audio) {
          this.audio.stop();
          await this.room.unpublish([this.audio]);
        }
        
        let mediaStream: MediaStream;
        try {
          mediaStream = await navigator.mediaDevices.getUserMedia({
            video: false,
            audio: this.constraints.audio
          });
        } catch(err) {
          const e = err as Error;
          const message = e.message;
          logger.error('Failed to switchMic. ', e.name, message);
    
          throw new ClientError(1203, `getUserMedia ì¤í¨(${message})`, e);
        }
        const [audioTrack] = mediaStream.getAudioTracks();

        if(audioTrack) {
          if(audioTrack.readyState === 'ended') {
            throw new ClientError(1205, 'ì¤ëì¤ í¸ëì íëíì§ë§ ì¢ë£ ìíìëë¤.'); 
          }

          this.audio = new LocalAudio(audioTrack, 'single');
          logger.debug('switch audio track', 'id=' + audioTrack.id, 'label=' + audioTrack.label, 'enabled=' + audioTrack.enabled, 'kind=' + audioTrack.kind, 'muted=' + audioTrack.muted, 'readyState=' + audioTrack.readyState);

          if(this.room) await this.room.publish([this.audio]);
        }
      }
    }
  }

  @Loggable('info')
  async _getUserMedia() {
    this.stop();

    try {
      const mediaStream = await navigator.mediaDevices.getUserMedia(this.constraints);
      const [audioTrack] = mediaStream.getAudioTracks();
      const [videoTrack] = mediaStream.getVideoTracks();

      logger.debug('got mediaStream', 'id=' + mediaStream.id, 'active=' + mediaStream.active);
      
      if(audioTrack) {
        logger.debug('got audio track', 'id=' + audioTrack.id, 'label=' + audioTrack.label, 'enabled=' + audioTrack.enabled, 'kind=' + audioTrack.kind, 'muted=' + audioTrack.muted, 'readyState=' + audioTrack.readyState);

        if(audioTrack.readyState === 'ended') {
          throw new ClientError(1205, 'ì¤ëì¤ í¸ëì íëíì§ë§ ì¢ë£ ìíìëë¤.'); 
        }
      }

      if(videoTrack) {
        logger.debug('got video track', 'id=' + videoTrack.id, 'label=' + videoTrack.label, 'enabled=' + videoTrack.enabled, 'kind=' + videoTrack.kind, 'muted=' + videoTrack.muted, 'readyState=' + videoTrack.readyState);

        if(videoTrack.readyState === 'ended') {
          throw new ClientError(1206, 'ë¹ëì¤ í¸ëì íëíì§ë§ ì¢ë£ ìíìëë¤.'); 
        }
      }

      this.audio = audioTrack ? new LocalAudio(audioTrack, 'single') : undefined;
      this.video = videoTrack ? new LocalVideo(videoTrack, 'multiple') : undefined;
    } catch(err) {
      const e = err as Error;
      const message = e.message;
      logger.error('Failed to getUserMedia. ', e.name, message);

      throw new ClientError(1203, `getUserMedia ì¤í¨(${message})`, e);
    }
  }

  @Loggable('info')
  stop() {
    this.audio?.stop();
    this.video?.stop();
  }

  _setRoom(room: Room) {
    this.room = room;
  }

  @Loggable('info')
  setMediaStream(mediaStream: MediaStream) {
    const [audioTrack] = mediaStream.getAudioTracks();
    const [videoTrack] = mediaStream.getVideoTracks();

    this.audio = audioTrack ? new LocalAudio(audioTrack, 'single') : undefined;
    this.video = videoTrack ? new LocalVideo(videoTrack, 'multiple') : undefined; 

    if(audioTrack) logger.debug('set media stream track', 'id=' + audioTrack.id, 'label=' + audioTrack.label, 'enabled=' + audioTrack.enabled, 'kind=' + audioTrack.kind, 'muted=' + audioTrack.muted, 'readyState=' + audioTrack.readyState);
    if(videoTrack) logger.debug('set media stream track', 'id=' + videoTrack.id, 'label=' + videoTrack.label, 'enabled=' + videoTrack.enabled, 'kind=' + videoTrack.kind, 'muted=' + videoTrack.muted, 'readyState=' + videoTrack.readyState);
  }
}

export { LocalMedia };